import { Injectable } from '@angular/core';

@Injectable()
export class Cache {
  /**
   * 最近一次缓存
   * 
   * @static
   * @memberof Cache
   */
  static lastCache = new Object();
  /**
   * 当前缓存
   * 
   * @static
   * @memberof Cache
   */
  static cache = new Object();
  /**
   * 读取缓存对象
   * 
   * @memberof Cache
   */
  get = function (key) {
    return Cache.cache[key];
  }
  /**
   * 写入缓存
   * 
   * @memberof Cache
   */
  set = function (key, value) {
    Cache.cache[key] = value;
  }
  /**
   * 删除缓存
   * 
   * @param {any} key 
   * @memberof Cache
   */
  remove(key) {
    delete Cache.cache[key];
  }
  /**
   * 备份缓存
   * 
   * @memberof Cache
   */
  backup() {
    Cache.lastCache = Cache.cache;
  }
  /**
   * 清空缓存
   * 
   * @memberof Cache
   */
  clear() {
    Cache.cache = new Object();
  }
}



