import { Component, OnInit } from '@angular/core';
import { NavController, ModalController } from 'ionic-angular';
import { FirstPage } from '../first/first';
import { ListPage } from '../list/list';
import { StockService } from '../../providers/stock.service';
import { Cache } from '../../cache/cache.global';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
  styles: ['home.scss'],
  providers: [StockService, Cache]
})
export class HomePage implements OnInit {

  /**
   * 是否初次显示
   * 
   * @type {boolean}
   * @memberof HomePage
   */
  showFirst: boolean = false;
	/**
   * 轮播列表
   * 
   * @type {*}
   * @memberof HomePage
   */
  sliders: any;
  /**
   * 分类项目
   * 
   * @type {*}
   * @memberof HomePage
   */
  categories: any;
  /**
   * 热销商品
   * 
   * @type {*}
   * @memberof HomePage
   */
  hotSail: any;

	/**
	 * Creates an instance of HomePage.
	 * @param {Cache} cache 
	 * @param {NavController} navCtrl 
	 * @param {StockService} stockService 
	 * @param {ModalController} modalCtrl 
	 * @memberof HomePage
	 */
  constructor(public cache: Cache,
    public navCtrl: NavController,
    private stockService: StockService,
    public modalCtrl: ModalController) {
  }

	/**
	 * 初始化
	 * 
	 * @memberof HomePage
	 */
  ngOnInit() {
    this.showFirst = this.cache.get('showFirst');
    !this.showFirst && this.presentModal();
    this.sliders = this.getSliders();
    this.categories = this.getCategories();
    this.hotSail = this.getHotSail();
  }

  /**
   * 获取轮播图片
   * 
   * @returns 
   * @memberof HomePage
   */
  getSliders() {
    return ['assets/img/bars/bar1.jpg',
      'assets/img/bars/bar2.jpg',
      'assets/img/bars/bar3.jpg'];
  }

  /**
   * 获取分类项目
   * 
   * @memberof HomePage
   */
  getCategories() {
    return [{ title: '兰芝系列', src: 'assets/img/items/lanzhi/logo.jpg' },
    { title: '春雨系列', src: 'assets/img/items/chunyu/logo.jpg' },
    { title: '悦诗风吟系列', src: 'assets/img/items/yueshifengyin/logo.jpg' },
    { title: '可莱丝系列', src: 'assets/img/items/kelaisi/2.jpg' },
    { title: '水光系列', src: 'assets/img/items/shuiguang/1.jpg' },
    { title: '蜗牛系列', src: 'assets/img/items/woniu/1.jpg' },
    { title: '雪花秀系列', src: 'assets/img/items/xuehuaxiu/logo.jpg' },
    { title: '后系列', src: 'assets/img/items/tire1.png' }]
  }

  /**
   * 获取热销商品
   * 
   * @returns 
   * @memberof HomePage
   */
  getHotSail() {
    let hot = [{ title: '睡眠面膜', src: 'assets/img/items/lanzhi/mm2.jpg' },
    { title: '可莱丝面膜', src: 'assets/img/items/kelaisi/3.jpg' },
    { title: '水光面膜', src: 'assets/img/items/shuiguang/7.jpg' }];
    return [hot, hot, hot];
  }

  /**
   * 获取后台数据
   * 
   * @memberof HomePage
   */
  getServerData() {
    this.stockService.getStock()
      .then(res => {
        console.info(res)
      })
      .catch(err => {
        console.error(err)
      })
  }

  goList(category) {
      this.navCtrl.push(ListPage);
  }

	/**
	 * 模态窗体
	 * 
	 * @memberof HomePage
	 */
  presentModal() {
    this.cache.set('showFirst', true);
    let modal = this.modalCtrl.create(FirstPage);
    modal.present({
      animate: false,
      duration: 0
    });
  }


}



